import React from 'react';
import Header from '../HeaderComponent/header';
import Footer from '../footerComponent/footer';

import './forma.css';

export default class Forma extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			premium: false,
		}

    this.togglePremium = this.togglePremium.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  handleSubmit(e) {
    e.preventDefault();
    console.log('submit');
  }

	togglePremium(e) {
		e.preventDefault();
		this.setState({ premium: !this.state.premium });
	}

	render() {
		return(
			<div className="narucistr">
				<Header />
				<div>
					<header id="formahead">
						<h1 className="formanaslov">uspomene.ba</h1>
						<br/> <br/>
						<h1 className="narudzbausluga">Narudžba usluga</h1>

					</header>
				</div>
				<div className="formaa">
					<form className="forma-front" onSubmit={this.handleSubmit} >
						Vaše ime i prezime (obavezno)<br/>
	   		    			<input type="text" className="imeprezime" required/><br/>
	   		    		Vaš telefon (obavezno)<br/>
	   		    			<input type="text" className="telefon" required/><br/>
	   		    		Vaš email (obavezno)<br/>
	   		    			<input type="text" className="email" required/><br/>
	   		    		Mjesto i adresa<br/>
	   		    			<input type="text" className="adresa"/><br/>
	   		    		Ime i prezime umrle osobe<br/> 
	   		    			<input type="text" className="umrlaosoba"/><br/>
	   		    		Datum i godina rođenja/smrti<br/>
	   		    			<input type="text" className="rodjenjeismrt"/><br/>
	   		    		Mjesto rođenja<br/>
	   		    			<input type="text" className="mjestorodjenja"/><br/>
	   		    		Vjeroispovjest<br/>
	   		    			<input type="text" className="vjeroispovjest"/><br/>
	   		    		Slike<br />
	   		    			<input type="file" className="slika" />
							<br />
	   		    			<button onClick={this.togglePremium} style={{marginTop: "5px", marginRight: "5px" }} className="btn btn-danger" >
	   		    				{ this.state.premium ? 'Free' : 'Premium'}
	   		    			</button>
	   		    	<div style={!this.state.premium ? {display: 'none'} : {display: 'block'}} >	
	   		    		Biografija umrle osobe <br/>
	   		    			<textarea className="bio"/><br/>
	   		    		Napomena (opcionalno) <br/>
	   		    			<textarea  className="napomena"/><br/>
	   		    	</div>
	   		    		{(this.state.premium)  ? (
	   		    			<input type='submit' value='Platite' className="btn btn-primary" />	
	   		    		
	   		    		) : (
	   		    			<input type="submit" value="Submit" className="btn btn-primary" style={{marginTop:"5px"}}/>
	   		    		)}
	   		    	</form>
	   		    </div>
	   		    <Footer />
   		   </div>
				
		);
	}
}


