import React from 'react';
import axios from 'axios';
import Api from './api';

import '../Styles/promjenaSifre.css';

export default class EditujUspomenu extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      id: '',
      ime: '',
      prezime: '',
      telefon: '',
      email: '',
      grad: '',
      adresa: '',
      premIme: '',
      premPrezime: '',
      biografija: '',
      religija: '',
      datumRodj: '',
      datumSmrti: '',
      mjRodj: '',
      mjSmrti: '',
      photo: [],
      premium: true,
      datumPlacanja: null,
      komentar: '',
      datumEdit: null,
      note: '',
    }

    this.editujIzmjenu = this.editujIzmjenu.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleCheckbox = this.handleCheckbox.bind(this);
    this.dated = this.dated.bind(this);
    this.handlePicture = this.handlePicture.bind(this);
  }

  handlePicture(e) {
    console.log(e.target);
    let self = this;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    const data = new FormData();
    data.append('file', e.target.files[0]);
    /*const config = {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'multipart/form-data'
      }
    }*/
    Api.post('api/pictureAbout', data)
    .then(res => {
      console.log(res);
      if(res.status === 200) {
        self.setState({ photo: res.data.photo });
      }
    })
    .catch(err => {
      console.log(err);
    });
  }

  dated(e) {
    console.log(typeof(e.target.value));
  }

  handleChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  handleCheckbox(e) {
    this.setState({ premium: !this.state.premium });
  }

  editujIzmjenu(e) {
    e.preventDefault();
    console.log(this.state);
    let ime = this.props.match.params.id;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.put("api/memories/" + ime, {
      memory: {
        personalInfo: {
          name: {
            first: this.state.ime,
            last: this.state.prezime
          },
          phone: this.state.telefon,
          email: this.state.email,
          place: {
            city: this.state.grad,
            address: this.state.adresa
          }
        },
        deceased: {
          name: {
            first: this.state.premIme,
            last: this.state.premPrezime
          },
          biography: this.state.biografija,
          religion: this.state.religija,
          dateOfBirth: this.state.datumRodj,
          dateOfDeath: this.state.datumSmrti,
          birthPlace: this.state.mjRodj,
          placeOfDeath: this.state.mjSmrti,
          photo: this.state.photo,
        },
        premium: {
          isPremium: this.state.premium,
          paidAt: this.state.datumPlacanja,
        },
        note: this.state,
        comment: this.state.komentar,
      }
    })
    .then(res => {
      if(res.status === 202) {
        alert("Uspjesno editovana uspomena!");
      }
      else {
        alert("Greska pri editovanju");
      }
    })
    .catch(err => {
      alert("Greska");
    });
  }

  componentDidMount() {
    let self = this;
    let id = this.props.match.params.id;
    axios.defaults.headers.common['Authorization'] = localStorage.getItem('jwtToken');
    Api.get("api/oneMemory/" + id)
    .then(res => {
      
      console.log(Date.parse(res.data.memory.lastEdited.split('T')[0]));
      let memoryOne = res.data.memory;
      let d = new Date(memoryOne.deceased.dateOfDeath);//.toISOString());
      console.log(d.toLocaleDateString());
      let a = new Date(d.toLocaleDateString());
      console.log(a.toISOString());
      
      self.setState({
        id: memoryOne._id,
        ime: memoryOne.personalInfo.name.first,
        prezime: memoryOne.personalInfo.name.last,
        email: memoryOne.personalInfo.email,
        telefon: memoryOne.personalInfo.phone,
        grad: memoryOne.personalInfo.place.city,
        adresa: memoryOne.personalInfo.place.address,
        premIme: memoryOne.deceased.name.first,
        premPrezime: memoryOne.deceased.name.last,
        biografija: memoryOne.deceased.biography,
        religija: memoryOne.deceased.religion,
        datumRodj: memoryOne.deceased.dateOfBirth,
        datumSmrti: memoryOne.deceased.dateOfDeath,
        mjRodj: memoryOne.deceased.birthPlace,
        mjSmrti: memoryOne.deceased.placeOfDeath,
        photo: memoryOne.deceased.photo,
        premium: memoryOne.premium.isPremium,
        datumPlacanja: memoryOne.premium.paidAt,
        komentar: memoryOne.comment,
        datumEdit: memoryOne.lastEdited,
        note: memoryOne.note,
      });
    })
    .catch(err => {
      console.log(err);
    });
  }

  render() {
    return(
      <div className="sifra" style={{ marginTop: "5%" }} >
        <form onSubmit={this.editujIzmjenu} >
          <h1>Podaci o naruciocu</h1>
          <label>Ime</label>
          <input type="text" onChange={this.handleChange} name="ime" value={this.state.ime} />
          <label>Prezime</label>
          <input type="text" onChange={this.handleChange} name="prezime" value={this.state.prezime} />
          <label>Telefon</label>
          <input type="text" onChange={this.handleChange} name="telefon" value={this.state.telefon} />
          <label>Email</label>
          <input type="text" onChange={this.handleChange} name="email" value={this.state.email} />
          <label>Grad</label>
          <input type="text" onChange={this.handleChange} name="grad" value={this.state.grad} />
          <label>Adresa</label>
          <input type="text" onChange={this.handleChange} name="adresa" value={this.state.adresa} />

          <h1>Podaci o preminulom</h1>
          <label>Ime</label>
          <input type="text" onChange={this.handleChange} name="premIme" value={this.state.premIme} />
          <label>Prezime</label>
          <input type="text" onChange={this.handleChange} name="premPrezime" value={this.state.premPrezime} />
          <label>Biografija</label>
          <textarea onChange={this.handleChange} name="biografija" value={this.state.biografija} ></textarea>
          <label>Religija</label>
          <input type="text" onChange={this.handleChange} name="religija" value={this.state.religija} />
          <label>Datum rodjenja</label>
          <input type="date" onBlur={this.dated} onChange={this.handleChange} name="datumRodj" value={this.state.datumRodj} required/>
          <label>Datum smrti</label>
          <input type="text" onChange={this.handleChange} name="datumSmrti" value={this.state.datumSmrti} />
          <label>Mjesto rodjenja</label>
          <input type="text" onChange={this.handleChange} name="mjRodj" value={this.state.mjRodj} />
          <label>Datum kada je dodana</label>
          <input type="text" onChange={this.handleChange} name="mjSmrti" value={this.state.mjSmrti} />
          { this.state.photo.map(slika => {
            return <img 
              src={`http://localhost:8000/${slika}`}
              alt="O nama"
              style={{ width: "200px", height: "200px", marginTop: "20px" }}  
            />
          })}
          <br />
          <input type="file" onChange={this.handlePicture} style={ !this.state.premium ? { display: "none" } : null } />

          <h1>Podaci o racunu</h1>
          <label>Premium</label>
          <label>Datum placanja</label>
          <input type="text" onChange={this.handleChange} name="datumPlacanja" value={this.state.datumPlacanja} />

          <h1>Biljeske</h1>
          <label>Zabiljeska</label>
          <input type="text" onChange={this.handleChange} name="note" value={this.state.note} />
          <label>Komentar</label>
          <textarea onChange={this.handleChange} name="komentar" value={this.state.komentar} ></textarea>
          <button type="submit" >Spasi izmjene</button>
        </form>
      </div>
    );
  }
}


