import React from 'react';
import './uspomena.css';
import Header from '../HeaderComponent/header';
import Footer from '../footerComponent/footer';
import Api from '../api';

export default class Uspomena extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			ime: '',
			prezime: "",
			dRodjenja: '',
			dSmrti: '',
			mRodjenja: "",
			mSmrti: "",
			vjera: "",
			slika: "",
		}
	}

	componentDidMount() {
		let self = this;
		console.log(this.props.match.params.id);
		Api.get("api/oneMemory/" + this.props.match.params.id)
		.then(res => {
			if(res.status === 200) {
				self.setState({
					ime: res.data.memory.deceased.name.first,
					prezime: res.data.memory.deceased.name.last,
					dRodjenja: res.data.memory.deceased.dateOfBirth,
					dSmrti: res.data.memory.deceased.dateOfDeath,
					mRodjenja: res.data.memory.deceased.birthPlace,
					mSmrti: res.data.memory.deceased.placeOfDeath,
					vjera: res.data.memory.deceased.religion
				});
			}
			else {
				alert("Greska");
			}
		})
		.catch(err => {
			alert("Greska");
		});
	}

	render() {
		return(
			<div style={{height: "100vh"}}>
				<Header />
				<div className="detalji" >
					
					<h1 className="nas2">{this.state.ime} {this.state.prezime}</h1>
					<br/>
					<br/>
					<div className="detalji2" >
						<div>
						<div className="karusel">
							<div id="carouselExampleControls" class="carousel slide" data-ride="carousel">
								<div className="carousel-inner">
									<div class="carousel-item active">
										<img className="d-block w-100 kSlika" src="../Slike/uspomena1.jpg" alt="First slide" />
									</div>
									<div className="carousel-item">
										<img className="d-block w-100 kSlika" src="../Slike/uspomena1.jpg" alt="Second slide" />
									</div>
									<div className="carousel-item">
										<img className="d-block w-100 kSlika" src="../Slike/logofinal.png" alt="Third slide" />
									</div>
								</div>
								<a className="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
									<span className="carousel-control-prev-icon" aria-hidden="true"></span>
									<span className="sr-only">Previous</span>
								</a>
								<a className="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
									<span className="carousel-control-next-icon" aria-hidden="true"></span>
									<span className="sr-only">Next</span>
								</a>
							</div>
						</div>
						</div>
						<div className="listadetalja">
							<ul>
								<li className="detalj">Godina rodjenja: {this.state.dRodjenja}</li>
								<li className="detalj">Godina smrti: {this.state.dSmrti}</li>
								<li className="detalj">Mjesto rodjenja: {this.state.mRodjenja}</li>
								<li className="detalj">Mjesto smrti: {this.state.mSmrti}</li>
								<li className="detalj">Vjeroispovjest: {this.state.vjera}</li>
							</ul>
						</div>
						
					</div>
					<div className="biografija">
						<br />
						<h3>Biografija:</h3>
						<p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.</p>
					</div>
					
				</div>
				<Footer style={{marginBottom: "0"}} />
			</div>
			
		);
	}
}


