import React, { Component } from 'react';
import { Link } from 'react-router-dom';


class Header extends Component {
  render() {
    return (
      <nav className="navbar navbar-expand-lg navbar-light">
        <div className="headeritem"><img className="logofinal" alt="logo" src="../Slike/logofinal.png"/></div>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto" style={{margin: "auto",}}>
            <li className="nav-item active">
              <Link className="nav-link" to="/">Home <span className="sr-only">(current)</span></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/uspomene/1">Uspomene</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/forma">Naruci</Link>
            </li>
          </ul>
        </div>
      </nav>
    );
  }
}

export default Header;


