import React from 'react';
import { Route, NavLink, Switch } from 'react-router-dom';

//import components
import DodajModeratora from './dodajModeratora';
import PromjenaSifre from './promjenaSifre';
import Moderatori from './Moderatori';
import Uspomene from './Uspomene';
import EditujUspomenu from './editujUspomenu';
import ONama from './oNama';
import Arhiva from './Arhiva';
import EditujModeratora from './editujModeratora';
import Narudzbe from './Narudzbe';

//import styles
import '../Styles/index.css';

export default class IndexCMS extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      permission: true,
    }
  }

  render() {
    return(
      <div className="index2" >
        <div className="navbar2" >
          <NavLink activeStyle={{ backgroundColor: 'darkblue', color: 'white', }} to="/cms/index/uspomene" >
            <i className="fas fa-id-card-alt"></i>Uspomene
          </NavLink>
          <NavLink activeStyle={{ backgroundColor: 'darkblue', color: 'white', }} to="/cms/index/narudzbe" >
            <i className="fas fa-archive"></i>Narudžbe
          </NavLink>
          <NavLink activeStyle={{ backgroundColor: 'darkblue', color: 'white', }} to="/cms/index/moderatori" >
            <i className="fas fa-address-card"></i>Moderatori
          </NavLink>
          {this.state.permission ?
            <NavLink activeStyle={{ backgroundColor: 'darkblue', color: 'white', }} to="/cms/index/oNama" >
              <i className="fas fa-info-circle"></i>O nama
            </NavLink>
            : null
          }
          <NavLink activeStyle={{ backgroundColor: 'darkblue', color: 'white', }} to="/cms/index/promjenaSifre" >
            <i className="fas fa-key"></i>Promjena šifre
          </NavLink>
          <NavLink activeStyle={{ backgroundColor: 'darkblue', color: 'white', }} to="/cms/index/arhiva" >
            <i className="fas fa-archive" ></i>Arhiva
          </NavLink>
          <NavLink to="#" onClick={this.signOut = () => {
            localStorage.removeItem('jwtToken');
            console.log(document.cookie);
            document.cookie = null;
            console.log(document.cookie);
            this.props.history.push('/cms');
          }} >
            <i className="fas fa-sign-out-alt"></i>Odjava
          </NavLink>{/*ovdje mrtav link treba*/}
          <button><i className="fas fa-bars"></i></button>
        </div>
        <div className="page" >
          <Switch>
            <Route path="/cms/index/uspomene" component={Uspomene} />
            <Route path="/cms/index/moderatori" component={Moderatori} />
            <Route path="/cms/index/dodajModeratora" component={DodajModeratora} />
            <Route path="/cms/index/promjenaSifre"component={PromjenaSifre} />
            <Route path="/cms/index/uspomena/:id" component={EditujUspomenu} />
            <Route path="/cms/index/moderator/:id" component={EditujModeratora} />
            <Route path="/cms/index/oNama" component={ONama} />
            <Route path="/cms/index/arhiva" component={Arhiva} />
            <Route path="/cms/index/narudzbe" component={Narudzbe} />
          </Switch>
        </div>
      </div>
    );
  }
}


