import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';

//import styles
import './App.css';

//components
import Login from './Components/Login';
import IndexCMS from './Components/index';

import Naslovna from './Components/naslovna/naslovna.js';
import Forma from './Components/Forma/Forma';
import PregledUspomena from './Components/PregledUspomena/PregledUspomena';
import Uspomena from './Components/uspomena/Uspomena';

class App extends Component {
  render() {
    return (
      <div>
        <Router>
          <div>
            <Route exact path="/" component={Naslovna} />
            <Route exact path="/forma" component={Forma} />
            <Route exact path="/uspomene/:id" component={PregledUspomena} />
            <Route exact path="/uspomena/:id" component={Uspomena} />
            <Route exact path='/cms' component={Login} />
            <Route path='/cms/index' component={IndexCMS} />
          </div>
        </Router>
      </div>
    );
  }
}

export default App;



